<template>
  <input :value="modelValue" @input="updateInput" type="text" name="" class="input">
</template>

<script>
export default {
    name: 'my-input',
    props: {
      modelValue: [String, Number]
    },
    methods: {
      updateInput(event) {
        this.$emit('update:modelValue', event.target.value)
      }
    }
}
</script>

<style scoped>
.input {
    width: 100%;
    border: 1px solid teal;
    margin: 5px 0;
    padding: 10px 15px;
}
</style>